* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.link {
  color: #333;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  line-height: 1.5;
  text-decoration: none;
  transition: background-color .2s;
}

.item:hover {
  background-color: #eee;
}

.btn {
  color: #fff;
  color: #fff;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  cursor: pointer;
  min-width: 150px;
  vertical-align: middle;
  text-align: center;
  background: #1d1d1d;
  border: 2px solid #1d1d1d;
  border-radius: 10px;
  outline: none;
  padding: 10px;
  font-size: 1rem;
  transition: color .1s cubic-bezier(.16, .08, .355, 1), background .1s cubic-bezier(.16, .08, .355, 1);
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.btn:hover {
  color: #fff;
  background: #2a2a2a;
  border-color: #2a2a2a;
}

.btn-border {
  color: #1d1d1d;
  background-color: rgba(0, 0, 0, 0);
}

.btn-arrow {
  transition: background-color .3s ease-out;
  position: relative;
}

.span {
  will-change: transform;
  transition: all .3s ease-out;
  display: inline-block;
  position: relative;
}

.btn-arrow:hover .span {
  transform: translate3d(-1rem, 0, 0);
}

.svg {
  width: 1.1em;
  opacity: 0;
  will-change: right, opacity;
  stroke-width: 5px;
  fill: rgba(0, 0, 0, 0);
  transition: all .3s ease-out;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.btn-arrow:hover .svg {
  opacity: 1;
  right: -2rem;
}

.container {
  height: 300px;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.button {
  height: 50px;
  min-width: 150px;
  color: #fff;
  text-transform: uppercase;
  z-index: 1;
  background: none;
  border-radius: 10px;
  padding: 10px;
  font-family: arial;
  font-size: 16px;
  font-weight: 700;
  line-height: 30px;
  transition: all .5s;
  position: relative;
}

.button:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  background: #1d1f20;
  border-radius: 10px;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: 0;
}

.button:after {
  content: "";
  height: calc(100% + 6px);
  width: calc(100% + 6px);
  z-index: -2;
  background: linear-gradient(60deg, #f79533, #f37055, #ef4e7b, #a166ab, #5073b8, #1098ad, #07b39b, #6fba82) 0 0 / 300% 300%;
  border-radius: 10px;
  animation: animatedgradient 3s infinite alternate;
  position: absolute;
  top: -3px;
  left: -3px;
}

.button:hover {
  cursor: pointer;
}

.button:hover:before {
  width: 0%;
}

.button:disabled {
  opacity: .5;
  cursor: not-allowed;
  color: gray;
}

.button:disabled:before, .button:disabled:after {
  transition: none;
  animation: none;
}

.button:disabled:hover:before {
  width: 100%;
}

@keyframes animatedgradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

